import { Block, CetusTooltip } from '@cetus/design'
import { Center, HStack, Image, Skeleton, Text, VStack } from '@chakra-ui/react'
import HiddenDotted from '../HiddenDotted'
import { ProfileTabsProps } from './type'

function PCProfileTabs({ tabList, activeTab, tabData, onClickTab }: ProfileTabsProps) {
  return (
    <HStack w="100%" mt="12px" gap="12px">
      {tabList.map((item: any) => (
        <Block
          key={item.value}
          w="25%"
          p="24px 16px"
          bgImage={activeTab === item.value ? 'url(/images/tab_active.png)' : 'none'}
          bgSize="100% 100%"
          borderRadius="16px"
          display="flex"
          justifyContent="space-between"
          alignItems="center"
          cursor="pointer"
          bg="bg_secondary"
          backdropFilter="blur(20px)"
          borderColor={activeTab === item.value ? 'primary' : 'transparent'}
          onClick={() => onClickTab(item.value)}
          _hover={{
            bgImage: 'url(/images/tab_active.png)',
            bgSize: '100% 100%',
            borderColor: 'primary',
            img: {
              opacity: '0.8'
            }
          }}
        >
          <VStack align="flex-start" gap="10px">
            <Text fontSize="14px" color="primary_gray">
              {item.title}
            </Text>
            <HStack h="22px">
              <HiddenDotted size="l">
                <CetusTooltip
                  placement="top"
                  tooltip={
                    <Text fontSize="12px" lineHeight="20px" color="text_caption">
                      {item.tooltip}
                    </Text>
                  }
                  showTooltip={item.showTooltip}
                >
                  <Center as="button">
                    <Skeleton isLoaded={!tabData[item.value]?.isLoading}>
                      <Text fontSize="20px" fontWeight="500" color="text_caption">
                        {tabData[item.value]?.totalValue}
                      </Text>
                    </Skeleton>
                  </Center>
                </CetusTooltip>
              </HiddenDotted>
            </HStack>
          </VStack>
          <Image w="38px" h="38px" src={item.activeImgUrl} opacity={activeTab === item.value ? '0.8' : '0.8'} />
        </Block>
      ))}
    </HStack>
  )
}

export default PCProfileTabs
